/* General Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  position: relative;
  font-family: Georgia, 'Times New Roman', Times, serif;
}

/* Header */
header {
  background-color: #333;
  color: #fff;
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

header .logo h1 {
  font-size: 24px;
}

header nav ul {
  list-style: none;
  display: flex;
}

header nav ul li {
  margin-left: 20px;
}

header nav ul li a {
  color: #fff;
  text-decoration: none;
  font-size: 16px;
}

/* Hero Section */
.hero {
  background-color: #0099ff;
  color: white;
  text-align: center;
  padding: 50px 20px;
}

.hero h2 {
  font-size: 36px;
  margin-bottom: 10px;
}

.hero p {
  font-size: 18px;
  margin-bottom: 20px;
}

.get-button {
  background-color: #ff4500;
  padding: 10px 20px;
  color: white;
  text-decoration: none;
  font-size: 18px;
  border-radius: 5px;
}

.get-button:hover {
  background-color: #e03e00;
}

/* Featured Courses Section */
.featured-courses {
  background-color: white;
  padding: 40px 20px;
  text-align: center;
}

.featured-courses h3 {
  font-size: 32px;
  margin-bottom: 20px;
}

.course-list1 {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.course {
  background-color: #eaeaea;
  padding: 20px;
  width: 250px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  cursor: pointer;

}

.course:hover {
  transform: scale(1.05);
  cursor: pointer;
}

.course h4 {
  font-size: 24px;
  margin-bottom: 10px;
}

.course p {
  font-size: 16px;
}

.cta-button1 {
  background-color: #1f80ee;
  color: white;
  padding: 20px 30px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

/* How It Works Section */
.how-it-works {
  padding: 40px 40px;
  text-align: center;
}

.how-it-works h3 {
  font-size: 32px;
  margin-bottom: 20px;
}

.steps {
  display: flex;
  justify-content: center;
  gap: 40px;
  cursor: pointer;

}

.step {
  background-color: #f0f0f0;
  padding: 20px;
  width: 200px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.step h4 {
  font-size: 24px;
  margin-bottom: 10px;
}

.step p {
  font-size: 16px;
}

.course-images {
  position: relative;
  width: 100%;
  height: 60%;
  border-radius: 10px;
}

header .nav-menu {
  display: none;
}

header .menu {
  width: 30px;
  padding: 3px;
  /* border: 1px solid blue; */
  background-color: #f0f0f0;
  margin: 2px;
  border-radius: 40px;
}


/* Footer */


/* Media Queries for Responsiveness */
@media (max-width: 768px) {
  * {
    font-size: 15px;
  }

  header #nav {
    flex-direction: column;
    align-items: center;
    display: none;
  }

  header .nav-menu {
    display: block;
  }

  ul {
    list-style: none;
    /* display: flex; */
  }

  ul li {
    margin-left: 20px;
  }

  ul li a {
    color: #fff;
    text-decoration: none;
    font-size: 16px;
  }

  .course-list1 {
    display: block;
    gap: 20px;
    display: relative;
  }

  .course-card {
    display: relative;
  }

  .course-card button {
    width: 80%;
  }

  how-it-works {
    display: block;
    text-align: center;
  }

  .steps {
    flex-direction: column;
    gap: 30px;

  }
}